
import { Component, Prop, Vue } from "vue-property-decorator"
import { RibbonMenuItem } from "@/components/RibbonMenu"

@Component({name: "Ribbon"})
export default class Ribbon extends Vue {
  @Prop({required: true})
  value!: string;

  @Prop({required: true})
  menu!: RibbonMenuItem[];

  get active() {
    return this.value
  }

  set active(newValue) {
    this.$emit('input', newValue)
  }
}
