























import { Component, Prop } from "vue-property-decorator"
import Vue from 'vue';

@Component({name: "FormulaBar"})
export default class FormulaBar extends Vue {
  active = false

  @Prop()
  value!: string;

  @Prop()
  label!: string

  get formula() {
    return this.value;
  }

  set formula(newFormula: string) {
    this.$emit('input', newFormula);
  }
}
