















import { Component, Prop } from "vue-property-decorator"
import Vue from 'vue';

@Component({name: "ColorPicker"})
export default class ColorPicker extends Vue {
  @Prop({default: '#ffffff'})
  value!: string;

  @Prop()
  label?: string;

  get color() {
    if (this.value === null) {
      return '#ffffff';
    }
    return this.value;
  }

  set color(newValue) {
    this.$emit('input', newValue)
  }
}
